___CSS_LOADER_EXPORT___.push([module.id, `.search-product {
  width: 100%;
  height: fit-content;
  background-color: rgb(197, 197, 197);
  box-shadow: rgba(0, 0, 0, 0.18) 0px 2px 4px;
}
.search {
  display: flex;
  /* justify-content: center; */
  align-items: center;
  width: fit-content;
  height: auto;
  padding: 14px 24px;
}

.search input {
  border-radius: 5px;
  padding: 10px;
}

.search i {
  border-radius: 5px;
  padding: 10px;
}

.fa-filter {
  font-size: 22px;
  cursor: pointer;
}

.product-list {
  list-style: none;
  padding: 0;
  margin: 0;
  border: 1px solid #ddd;
  border-radius: 5px;
  max-width: 600px;
}

.product-list li {
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.product-list li:last-child {
  border-bottom: none;
}
